import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {
	extensionSplit,
	filterAriaAttributes
} from '@servicenow/library-enhanced-utils';
import styles from './_now-split-button.scss';
import focusBehavior from '@servicenow/behavior-focus';
import '@servicenow/now-icon';
import '@servicenow/now-dropdown';
import '@servicenow/now-popover';
import '@servicenow/now-tooltip';
import {itemsSchema, itemSchema} from '@servicenow/now-dropdown';
import {configAriaSchema} from '@servicenow/library-schemas';
import {t} from 'sn-translate';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const getFocusedElement = (event) => event.target.shadowRoot.activeElement;

const getActionButtonEl = (host) => {
	const componentId = host.getAttribute('component-id');
	return host.shadowRoot.getElementById(`action-${componentId}`);
};

const getTriggerButtonEl = (host) => {
	const componentId = host.getAttribute('component-id');
	return host.shadowRoot.getElementById(`trigger-${componentId}`);
};

const setActiveItem = (items, updateState) => {
	if (Array.isArray(items) && items.length) {
		updateState({activeItem: items[0].id});
	}
};

const setRef = (name, ref, updateState) =>
	setTimeout(() => updateState({[name]: ref}));

const renderIcon = (icon, size, label) => {
	if (icon) {
		return (
			<now-icon
				class={{
					'now-m-inline-end--xxs': label && size !== 'lg',
					'now-m-inline-end--xs': label && size === 'lg'
				}}
				icon={icon}
				size={size}
				aria-hidden="true"
			/>
		);
	}
	return '';
};

const renderActionButton = (state, dispatch, updateState, tooltipContent) => {
	const {componentId} = state;
	const {
		variant,
		size,
		disabled,
		label,
		icon = state.properties.iconName,
		configAriaAction,
		configAria,
		opened
	} = state.properties;

	const configAriaToUse = (configAria && configAria.action) || configAriaAction;

	return (
		<button
			type="button"
			id={`action-${componentId}`}
			on-click={(e) => {
				if (!opened) {
					e.stopPropagation();
				}
				if (!disabled) {
					dispatch(() => {
						return {
							type: 'NOW_SPLIT_BUTTON#ACTION_CLICKED',
							payload: {}
						};
					});
				}
			}}
			class={{
				'now-split-button-action': true,
				...(!disabled ? extensionSplit(variant) : {}),
				['-' + size]: size,
				'is-disabled': disabled,
				'only-icon': icon && !label
			}}
			hook-insert={({elm}) => setRef('actionRef', elm, updateState)}
			hook-destroy={() => setRef('actionRef', null, updateState)}
			{...filterAriaAttributes(configAriaToUse)}
			aria-disabled={String(disabled)}
			aria-describedby={
				tooltipContent ? `tooltip-${state.componentId}-action` : undefined
			}
			tabindex={disabled && !tooltipContent ? '-1' : '0'}
			on-mousedown={disabled ? (e) => e.preventDefault() : undefined}>
			<slot>
				{renderIcon(icon, size, label)}
				{label && <span className="now-line-height-crop">{label}</span>}
			</slot>
		</button>
	);
};

const renderTriggerButton = (state, updateState, tooltipContent) => {
	const {componentId, activeItem} = state;
	const {
		variant,
		size,
		disabled,
		configAriaTrigger,
		configAria,
		opened,
		items,
		showPresencePattern
	} = state.properties;

	const configAriaToUse =
		(configAria && configAria.trigger) || configAriaTrigger;

	return (
		<now-popover
			hide-tail
			manage-opened
			opened={opened}
			interaction-type="listbox"
			position-target={state.positionRef}
			constrain={{minWidth: 'target', maxWidth: 400}}
			disable-scroll>
			<button
				type="button"
				slot="trigger"
				id={`trigger-${componentId}`}
				class={{
					'now-split-button-trigger': true,
					'only-icon': true,
					...(!disabled ? extensionSplit(variant) : {}),
					['-' + size]: size,
					'is-disabled': disabled
				}}
				hook-insert={({elm}) => setRef('triggerRef', elm, updateState)}
				hook-destroy={() => setRef('triggerRef', null, updateState)}
				aria-label={t('Other actions')}
				{...filterAriaAttributes(configAriaToUse)}
				aria-disabled={String(disabled)}
				aria-describedby={
					tooltipContent ? `tooltip-${state.componentId}-trigger` : undefined
				}
				aria-haspopup={disabled ? 'false' : 'menu'}
				aria-expanded={opened ? 'true' : 'false'}
				tabindex={disabled && !tooltipContent ? '-1' : '0'}
				on-mousedown={disabled ? (e) => e.preventDefault() : undefined}>
				{renderIcon('caret-down-fill', size)}
			</button>
			<now-dropdown-list
				slot="content"
				style={{minHeight: '32px', maxHeight: '300px'}}
				items={items}
				select="none"
				active-item={activeItem}
				manage-active-item
				show-presence-pattern={showPresencePattern}
			/>
		</now-popover>
	);
};

const renderActionTooltip = (state, content) => {
	if (content) {
		return (
			<now-tooltip
				id={`tooltip-${state.componentId}-action`}
				target-ref={state.actionRef}
				content={content}
			/>
		);
	}
};

const renderTriggerTooltip = (state, content) => {
	if (content) {
		return (
			<now-tooltip
				id={`tooltip-${state.componentId}-trigger`}
				target-ref={state.triggerRef}
				content={content}
			/>
		);
	}
};

const view = (state, {dispatch, updateState}) => {
	const {tooltipContent} = state.properties;
	const actionTooltipContent =
		typeof tooltipContent === 'string'
			? tooltipContent
			: tooltipContent && tooltipContent.action;
	const triggerTooltipContent = tooltipContent && tooltipContent.trigger;

	return (
		<span
			className="now-split-button"
			role="group"
			hook-insert={({elm}) => setRef('positionRef', elm, updateState)}
			hook-destroy={() => setRef('positionRef', null, updateState)}>
			{renderActionButton(state, dispatch, updateState, actionTooltipContent)}
			{renderTriggerButton(state, updateState, triggerTooltipContent)}
			{renderActionTooltip(state, actionTooltipContent)}
			{renderTriggerTooltip(state, triggerTooltipContent)}
		</span>
	);
};

/**
 * A button variation that provides a primary action and a dropdown panel with related actions.
 *
 * ```jsx
 * <now-split-button label="Save" variant="primary" size="lg" items={[
 *   {id: 'update', label: 'Update Record'},
 *   {id: 'edit', label: 'Edit Record'},
 *   {id: 'delete', label: 'Delete Record'}
 * ]}></now-split-button>
 * <now-split-button icon="cog" items={[
 *   {id: 'foo', label: 'Foo Bar'}
 * ]}></now-split-button>
 * <now-split-button label="Disabled button" disabled items={[
 *   {id: 'foo', label: 'Foo Bar'},
 *   {id: 'baz', label: 'Baz Qux'}
 * ]}></now-split-button>
 * ```
 *
 * @seismicElement now-split-button
 * @summary Button variation with a primary action and dropdown panel with related actions.
 * @uib.label Split button
 * @uib.icon split-button
 * @uib.description A button variation that provides a primary action and a dropdown panel with related actions
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties variant, size, label, items, icon, disabled, opened, tooltipContent, showPresencePattern, configAria
 * @uib.actions NOW_SPLIT_BUTTON#OPENED_SET, NOW_SPLIT_BUTTON#ITEM_CLICKED, NOW_SPLIT_BUTTON#ACTION_CLICKED
 * @uib.slots
 */
createEnhancedElement('now-split-button', {
	properties: {
		/**
		 * Text to display inside the primary action button.
		 * @type {string}
		 * @uib.defaultValue Button
		 * @uib.description Button's inner label
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * An array of objects that define related actions in the dropdown panel.
		 * @type {(Array.<DropdownItem> | Array.<DropdownSection>)}
		 * @uib.description An array of objects that define related actions in the dropdown panel
		 * @uib.defaultValue [{"id": "bar", "label": "Baz"},{"id": "foo", "label": "Foo"},{"id": "qux", "label": "Qux"}]
		 * @uib.typeMetadata.emptyStateBehavior EMPTY_ARRAY
		 */
		items: {default: [], schema: itemsSchema},
		/**
		 * Sets the button styles, including colors and interaction behaviors.
		 * @type {("primary"|"primary-positive"|"primary-negative"|"secondary"|"secondary-positive"|"secondary-negative"|"tertiary")}
		 * @uib.label Variant
		 * @uib.description Color and behavior of the button
		 */
		variant: {
			default: 'secondary',
			schema: {
				type: 'string',
				enum: [
					'primary',
					'primary-positive',
					'primary-negative',
					'secondary',
					'secondary-positive',
					'secondary-negative',
					'tertiary'
				]
			}
		},
		/**
		 * Sets the size of the primary and additional action buttons.
		 * @type {('sm'|'md'|'lg')}
		 */
		size: {default: 'md', schema: {type: 'string', enum: ['sm', 'md', 'lg']}},
		/** @private */
		iconName: {},
		/**
		 * If defined, specifies the icon to display in the primary action
		 * button. See the `now-icon` component documentation for valid inputs.
		 * @type {string}
		 * @uib.label Icon
		 * @uib.description Select an icon name
		 * @uib.defaultValue magnifying-glass-outline
		 * @uib.fieldType icon
		 */
		icon: {schema: {type: 'string'}},
		/**
		 * When true, mutes the button color and prevents user click interactions.
		 * @type {boolean}
		 * @uib.label Disabled
		 * @uib.description Disable the button. Mutes colors and prevents click interactions
		 */
		disabled: {default: false, schema: {type: 'boolean'}},
		/**
		 * Automatically updated when the user opens or closes the related actions
		 * dropdown panel. Use `manage-opened` to override the default behavior and handle
		 * the `NOW_SPLIT_BUTTON#OPENED_SET` action manually.
		 * @type {boolean}
		 */
		opened: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * An object whose keys reference a specific HTML element within the
		 * now-split-button. The value of each key is an object that contains an ARIA
		 * property (or properties) and corresponding value/s. These ARIA
		 * properties are set on the specified element.
		 * The 'action' key corresponds to the main action button in now-split-button.
		 * The 'trigger' key corresponds to the dropdown trigger button in now-split-button.
		 * @type {{ action: {'aria-*': string}, trigger: {'aria-*': string} }}
		 * @uib.label ARIA properties
		 * @uib.description Configure ARIA properties
		 * @uib.defaultValue {"action": {"aria-label": "ARIA label"}, "trigger": {"aria-label": "ARIA label"}}
		 */
		configAria: {
			schema: {
				type: 'object',
				properties: {
					action: configAriaSchema,
					trigger: configAriaSchema
				},
				additionalProperties: false
			}
		},
		/**
		 * Deprecated, use `config-aria` instead
		 * @private
		 */
		configAriaAction: {
			schema: configAriaSchema
		},
		/**
		 * Deprecated, use `config-aria` instead
		 * @private
		 */
		configAriaTrigger: {
			schema: configAriaSchema
		},
		/**
		 * An object whose keys reference a specific HTML element within the
		 * now-split-button. The value of each key is a string that is
		 * displayed in a tooltip associated with that element.
		 * The 'action' key corresponds to the main action button in now-split-button.
		 * The 'trigger' key corresponds to the dropdown trigger button in now-split-button.
		 * @type {{ action: string, trigger: string }}
		 * @uib.label Tooltip text
		 * @uib.fieldType json
		 * @uib.defaultValue {action: 'Primary action', trigger: 'More actions'}
		 * @uib.description Text to display in a tooltip when hovering over either of the buttons
		 */
		tooltipContent: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							action: {type: 'string', translatable: true},
							trigger: {type: 'string', translatable: true}
						}
					},
					{type: 'string', translatable: true}
				]
			}
		},
		/**
		 * When true, displays the dropdown item's presence status icon with a pattern to make
		 * it distinguishable by a means other than color.
		 * @type {boolean}
		 * @uib.description Visual pattern enabled on the presence indicator for accessibility
		 */
		showPresencePattern: {default: false, schema: {type: 'boolean'}}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks on the primary action button.
		 * @type {{}}
		 * @uib.description Dispatched when the user clicks on the primary action button
		 */
		'NOW_SPLIT_BUTTON#ACTION_CLICKED': {
			schema: {
				type: 'object'
			}
		},
		/**
		 * Dispatched when the user clicks on one of the related actions
		 * in the dropdown panel. Payload contains a reference to the item's ID.
		 * @type {{item: DropdownItem}}
		 * @uib.description Dispatched when the user clicks on one of the related actions in the dropdown panel
		 */
		'NOW_SPLIT_BUTTON#ITEM_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					item: itemSchema
				}
			}
		},
		/**
		 * Dispatched when the user opens or closes the dropdown panel.
		 * Payload contains the value `true` when opened, or `false` when closed.
		 * Set the `manage-opened` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: boolean}}
		 * @uib.description Dispatched when the user opens or closes the dropdown panel
		 */
		'NOW_SPLIT_BUTTON#OPENED_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'boolean'}
				}
			}
		}
	},
	slots: {
		/**
		 * Content placed in the default slot appears inside the action button
		 * instead of the `icon` and/or `label`.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	eventHandlers: [
		{
			events: ['keydown'],
			effect({host, action, dispatch, properties, updateState}) {
				const {event} = action.payload;
				const {key} = event;

				if (key === 'ArrowUp' || key === 'ArrowDown') {
					action.preventDefault();
					const focused = getFocusedElement(event);
					const actionButton = getActionButtonEl(host);
					const triggerButton = getTriggerButtonEl(host);

					if (focused === actionButton || focused === triggerButton) {
						setActiveItem(properties.items, updateState);
						dispatch(() => {
							return {
								type: 'NOW_SPLIT_BUTTON#OPENED_SET',
								payload: {value: true}
							};
						});
					}
				}

				if (key === 'ArrowLeft' || key === 'ArrowRight') {
					action.preventDefault();
					const focused = getFocusedElement(event);
					const actionButton = getActionButtonEl(host);
					const triggerButton = getTriggerButtonEl(host);

					if (focused === actionButton) {
						triggerButton.focus();
					}
					if (focused === triggerButton) {
						actionButton.focus();
					}
				}
			}
		}
	],
	actionHandlers: {
		'NOW_POPOVER#OPENED_SET': ({action, dispatch, properties, updateState}) => {
			const {value} = action.payload;
			const {disabled, items} = properties;
			if (disabled) return;
			if (value) {
				setActiveItem(items, updateState);
			}
			dispatch(() => {
				return {
					type: 'NOW_SPLIT_BUTTON#OPENED_SET',
					payload: {value}
				};
			});
		},
		'NOW_POPOVER#CONTENT_VISIBLE': ({host}) => {
			const dropdownList = host.shadowRoot.querySelector('now-dropdown-list');
			dropdownList.focus();
		},
		'NOW_DROPDOWN_LIST#ITEM_CLICKED': ({dispatch, action}) => {
			dispatch(() => ({
				type: 'NOW_SPLIT_BUTTON#ITEM_CLICKED',
				payload: {item: action.payload.item}
			}));
			dispatch(() => {
				return {
					type: 'NOW_SPLIT_BUTTON#OPENED_SET',
					payload: {value: false}
				};
			});
		},
		'NOW_DROPDOWN_LIST#ACTIVE_ITEM_SET': ({action, updateState}) => {
			const activeItem = action.payload.value;
			updateState({activeItem});
		}
	},
	behaviors: [{behavior: focusBehavior}],
	styles,
	view
});
